<template>
  <div v-wechat-title="title">
    <div class="over-scroll" ref="wrapper" :style="{ height: wrapperHeight + 'px' }">
      <mt-loadmore :top-method="loadTop" :autoFill=false ref="loadmore">
        <div>
          <div class="flex padding-l-r-30 bg-ff height-88 margin-t-30" @click="onClick_moreItem(item)" v-for="(item,index) in cateGory.list" :key="index">
            <div class="iconfont margin-r-20 f-34 color-2d" :class="item.logoName||'icon-icon_list_recommend'"></div>
            <div class="flex-1 text-left f-34">{{item.cateGoryName}}</div>
            <div class="flex" v-if='item.audioManagerCount'>
              <div class="f-30 margin-r-20 color-b4">{{item.audioManagerCount}}</div>
              <div class="iconfont icon-disclosure_arrow_gra f-28"></div>
            </div>
            <div class="color-b4" v-else></div>
          </div>
        </div>
      </mt-loadmore>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "AllClassification",
  data() {
    return {
      title: "",
      // 
      wrapperHeight: 0
    };
  },
  computed: {
    ...mapGetters(["cateGory", "shareCfg", "reqCfg"])
  },
  mounted() {
    this.$nextTick(() => {
      this.title = this.$route.query.title;
      this.wrapperHeight = document.documentElement.clientHeight;
      this.$store.commit("getCateGoryList");
      this.$store.commit("userShare", {
        title: this.shareCfg.title,
        desc: this.shareCfg.desc,
        link: `${this.reqCfg.webUrl}?fromOpenId=${sessionStorage.getItem("openId")}`,
        imgUrl: this.shareCfg.imgUrl
      });
    });
  },
  methods: {
    onClick_moreItem(item) {
      item.audioManagerCount &&
        this.pushPage("Category", {
          title: item.cateGoryName,
          categoryId: item.cateGoryId
        });
    },
    // 
    loadTop() {
      // 
      setTimeout(() => {
        this.getcateGoryList(() => {
          this.$refs.loadmore.onTopLoaded();
        });
      }, 1000);
    }
  }
};
</script>


