<template>
  <div class='p-fixed bg-ff height-100p width-100p'>
    <img class="QrCodeImg border-radius-50p" :src="qrCfg.imgUrl">
    <div class='f-34 margin-t-20 paddig-t-15 font-bold'>{{qrCfg.platform}}</div>
    <div class='margin-t-30 padding-t-25 f-28'>{{qrCfg.desc}}</div>
    <img class="QRCode" v-if='qrcodeTicket' :src="`https://mp.weixin.qq.com/cgi-bin/showqrcode?ticket=${qrcodeTicket}`">
    <div class='color-2d f-28'> </div>
  </div>
</template>
<script>
import { mapGetters } from "vuex";
export default {
  name: "QrCode",
  data() {
    return {
      title: ""
    };
  },
  computed: {
    ...mapGetters(["qrCfg", "qrcodeTicket"])
  },
  mounted() {
    this.$nextTick(() => {
      this.$store.commit("getQrCode", this.$route.query.fromOpenId);
    });
  }
};
</script>
<style scoped>
.QrCodeImg {
  margin-top: 1rem;
  width: 1.4rem;
}
.QRCode {
  width: 3rem;
  margin-top: 1rem;
}
</style>




