import Vue from "vue";

// vue
import List from "./List.vue";
import VIPNav from "./VIPNav.vue";
import PriceList from "./PriceList.vue";
import QRFriends from "./QRFriends.vue";
import GreatVip from "./GreatVip.vue";
import Pay from "./Pay.vue";

// 
const useAssembly = {
  install(Vue) {
    Vue.component("List", List);
    Vue.component("VIPNav", VIPNav);
    Vue.component("PriceList", PriceList);
    Vue.component("QRFriends", QRFriends);
    Vue.component("GreatVip", GreatVip);
    Vue.component("Pay", Pay);
  }
};

export default useAssembly;

// 
Vue.use(useAssembly);



