import Vue from "vue";
import { Toast, Indicator } from "mint-ui";
/*
  -
    - this.pushPage('Home',{name:''})
  -
    - this.pushPage('Home',{name:''})
  -
    - this.back(-5) -1
  
    - this.scrollTop()
  
    - this.Toast('',2000,null,'middle')
  
    - this.showIndicator('...','fading-circle')
  
    - this.closeIndicator()
*/
// -
Vue.prototype.pushPage = function(val, parameter = null) {
  this.$router.push({
    path: `/${val}`,
    query: parameter
  });
};
// -
Vue.prototype.replacePage = function(val, parameter = null) {
  this.$router.replace({
    path: `/${val}`,
    query: parameter
  });
};
// -
Vue.prototype.back = function(val = -1) {
  this.$router.go(val);
};
// 
Vue.prototype.scrollTop = function() {
  document.documentElement.scrollTop = 0; //
  document.body.scrollTop = 0; //
};
// 
Vue.prototype.Toast = function(message, duration = 2000, iconClass, position = "middle") {
  Toast({
    message,
    position,
    iconClass,
    duration
  });
};
// 
Vue.prototype.showIndicator = function(text = "...", spinnerType = "fading-circle") {
  Indicator.open({
    text,
    spinnerType
  });
};
// 
Vue.prototype.closeIndicator = function() {
  Indicator.close();
};
// 
Vue.prototype.loadMore = function(load) {
  if (this.$store.getters.friend.count && !load) {
    this.showIndicator();
    this.$store.commit("stopGetFriend");
    setTimeout(() => {
      this.$store.commit("getShareDetailsManager", {
        callback: () => {
          this.closeIndicator();
        }
      });
    }, 100);
  }
};



