import Vue from "vue";
import Router from "vue-router";
import mobileRoutes from "./mobileRoutes";
import pcRoutes from "./pcRoutes";

Vue.use(Router);

let routes = null;

//pc
const userAgent = navigator.userAgent.toLowerCase();
const isIPad = String(userAgent.match(/ipad/i)) === "ipad";
const isIPhoneOs = String(userAgent.match(/iphone os/i)) === "iphone os";
const isMidp = String(userAgent.match(/midp/i)) === "midp";
const isUc7 = String(userAgent.match(/rv:1.2.3.4/i)) === "rv:1.2.3.4";
const isUc = String(userAgent.match(/ucweb/i)) === "ucweb";
const isAndroid = String(userAgent.match(/android/i)) === "android";
const isCE = String(userAgent.match(/windows ce/i)) === "windows ce";
const isWM = String(userAgent.match(/windows mobile/i)) === "windows mobile";
if (isIPad || isIPhoneOs || isMidp || isUc7 || isUc || isAndroid || isCE || isWM) {
  document.getElementsByTagName("html")[0].style.backgroundColor = "#f5f5f5";
  routes = mobileRoutes; // mobileRoutes h5
} else {
  document.getElementsByTagName("html")[0].style.backgroundColor = "#fff";
  routes = mobileRoutes; // pcRoutes pc
}

export default new Router({
  mode: "hash",
  routes
});



