import $ from "@/store/function";
const share = {
  state: {
    reqCfg,
    shareCfg,
    qrCfg
  },
  mutations: {
    shareConfig(state, val) {
      const json = { url: encodeURIComponent(location.href.split("#")[0]) };
      $.ajax("get", "WeiXin/getWXConfig", json, res => {
        wx.config({
          debug: false, // ,apialertpclogpc
          appId: state.reqCfg.appId, // 
          timestamp: res.data.timestamp, // 
          nonceStr: res.data.nonceStr, // 
          signature: res.data.signature, // 
          jsApiList: [
            "onMenuShareTimeline", // 1.0
            "onMenuShareAppMessage", // 1.0
            "onMenuShareQQ", //QQ 1.0
            "onMenuShareQZone", //QQ 1.0
            "updateTimelineShareData", ///QQ 1.4
            "updateAppMessageShareData", ///QQ 1.4
            "onMenuShareWeibo" //
          ] // JS
        });
        val.callback && val.callback();
      });
    },
    userShare(state, val) {
      const shareData = {
        title: val.title, // 
        desc: val.desc, // 
        link: val.link, // JS
        imgUrl: val.imgUrl, // 
        success(res) {
          val.callback && val.callback(res);
        },
        cancel(res) {
          val.errcallback && val.errcallback(res);
        }
      };
      wx.ready(() => {
        //
        if (wx.onMenuShareAppMessage) {
          wx.onMenuShareTimeline(shareData);
          wx.onMenuShareAppMessage(shareData);
          wx.onMenuShareQQ(shareData);
          wx.onMenuShareQZone(shareData);
        } else {
          wx.updateTimelineShareData(shareData);
          wx.updateAppMessageShareData(shareData);
        }
        wx.onMenuShareWeibo(shareData);
      });
    }
  }
};

export default share;



