<template>
    <div v-wechat-title="title" v-show='showPage'>
        <img class="width-100p" v-show='defaultImg' :src="courseInformation && courseInformation.imageUrl||defaultImg">
        <div class="bg-ff padding-all-30 flex" v-if='classHourOfThieAudio.count'>
          <mt-range class='flex-1' v-model="rangeValue" :min="0" :max="maxValue" :step="1" :bar-height="4">
            <div slot="start">
              <div class="iconfont f-44 margin-r-10" :class="{'icon-detail_pause color-2d':status&& !roateStatus,'icon-detail_play color-b4':!status&& !roateStatus,'icon-loading roate-5 color-2d':roateStatus}" @click="status = !status"></div>
            </div>
            <div slot="end">
              <div class="margin-l-20">{{rangeValue|formatHMS}} / {{singleClassHour.classHourLength|formatTimeDZ}}</div>
            </div>
          </mt-range>
          <audio id='audio' class="flex-1 flex flex-align-center height-100" :src="singleClassHour.wavUrl||''" autoplay @playing="playing" @timeupdate="timeupdate" @ended="ended" controlsList="nodownload" />
          <div class="flex flex-align-center iconfont icon-detail_good f-50 margin-l-30" :class="singleClassHour.snap?'color-2d':'color-b4'" @click='snapCount()'></div>
        </div>
        <div class='margin-t-30 bg-ff'>
          <div class="border-b flex f-34 color-96 bg-ff height-88">
            <div class="flex-1 flex mar-t margin-r-10 height-88">
              <div class='flex-1'></div>
              <div class="w-text" :class="{'color-00 font-bold border-h':selected==='BriefIntroduction'}" @click='selected="BriefIntroduction"'></div>
            </div>
            <div class="flex-1 flex mar-t margin-l-10 height-88">
              <div class="w-text" :class="{'color-00 font-bold border-h':selected==='Catalog'}" @click='selected="Catalog"'></div>
              <div class='flex-1'></div>
              <div class='iconfont flex flex-align-center margin-r-30' v-if='classHourOfThieAudio.count' :class="listSort?'icon-detail_down':'icon-detail_up'" @click='listSort=!listSort,$store.commit("reverseClassHourOfThieAudio")'></div>
            </div>
          </div>
          <div v-show='selected=="BriefIntroduction"'>
            <div v-if='courseInformation.information' class="f-30 color-33 line-42 padding-all-30 text-justify" v-html="courseInformation.information"></div>
            <div class="height-88 f-30 color-b4" v-else></div>
          </div>
          <div v-show='selected=="Catalog"'>
            <div v-if='classHourOfThieAudio.count'>
              <div class='flex margin-l-r-25 padding-t-b-10' :class="{'border-t':index}" @click='getClassHour(listSort?index:classHourOfThieAudio.count-index-1)' v-for="(item,index) in classHourOfThieAudio.list" :key='index'>
                <div class="flex flex-align-center margin-r-20 flex-pack-center w-index f-38" :class='{"color-b4": !item.free,"iconfont icon-loading roate-5": (listSort?index:classHourOfThieAudio.count-index-1)==whichOne && roateStatus,"color-2d":(listSort ?index:classHourOfThieAudio.count-index-1)==whichOne}'>
                  <div>
                    {{(listSort?index:classHourOfThieAudio.count-index-1)==whichOne && status?'':(listSort?index+1:classHourOfThieAudio.count-index)}}
                  </div>
                  <div v-if="(listSort?index:classHourOfThieAudio.count-index-1)==whichOne &&(!roateStatus && status)">
                    <img class='width-70p' src="@/assets/playing.gif">
                  </div>
                </div>
                <div class="flex-1 flex-align-center text-left f-34 text-ellipsis margin-r-20" :class="{'color-2d':whichOne===(listSort?index:classHourOfThieAudio.count-index-1)}">
                  <div class="hang-w text-ellipsis f-30" :class='{"color-b4": !item.free}'>{{item.title}}</div>
                  <div class="flex height-30 padding-t-10 f-24 color-b4">
                    <div class="iconfont icon-detail_listened f-24 margin-r-10"></div>
                    <div class="margin-r-30">{{item.alreadyHeardCount}}</div>
                    <div class="iconfont icon-detail_good f-24 margin-r-10"></div>
                    <div>{{item.snapCount}}</div>
                  </div>
                </div>
                <div class="flex flex-align-center" v-if='item.free'>
                  <div class="iconfont icon-icon_list_time margin-r-10 f-20" :class="{'color-2d':whichOne===(listSort?index:classHourOfThieAudio.count-index-1)}"></div>
                  <div class="f-24 color-b4 font-lighter" :class="{'color-2d':whichOne===(listSort?index:classHourOfThieAudio.count-index-1)}">{{item.classHourLength | formatTimeDZ}}</div>
                </div>
                <div class="iconfont icon-detail_lock margin-t-10 margin-l-r-30 color-b4" v-else></div>
              </div>
            </div>
            <div class="height-88 f-30 color-b4" v-else></div>
          </div>
        </div>
        <div class="p-fixed Details-tab width-100p height-88 bg-ff border-t" v-if='!userIsVIP.vip'>
          <div class="flex f-34">
            <div class="flex-1" @click='Auditions()'></div>
            <div class="flex-1 bg-2d color-ff" @click='status=false,$store.commit("openPaylist")'>VIP</div>
          </div>
        </div>
        <button ref='mouse' id='moveDiv' class='p-fixed backHome border-radius-50p bg-2d color-ff f-34 opacity-7' @mousedown="down" @touchstart="down" @mousemove.prevent="move" @touchmove.prevent="move" @mouseup="end" @touchend="end" @click='pushPage("/Home")'></button>
    </div>
</template>
<script>
var refresh = false; // 
import { MessageBox } from "mint-ui";
import { mapGetters, mapMutations } from "vuex";
export default {
  name: "Details",
  data() {
    return {
      showPage: window.name,
      title: "",
      // 
      defaultImg: null,
      // 
      selected: "Catalog",
      // 
      listSort: true,
      // 
      whichOne: -1,
      // 
      listSort: true,
      // 
      rangeValue: 0,
      maxValue: 0,
      // audio
      status: false,
      roateStatus: true,
      // 
      firstEnter: true,
      // 
      selected: "Catalog",
      // 
      defaultImg: "",
      // 
      Timeout: null,
      // 
      flags: false,
      position: { x: 0, y: 0 },
      nx: "",
      ny: "",
      dx: "",
      dy: "",
      xPum: "",
      yPum: ""
    };
  },
  computed: {
    ...mapGetters([
      "userInfo",
      "courseInformation",
      "classHourOfThieAudio",
      "singleClassHour",
      "userIsVIP",
      "shareCfg",
      "reqCfg"
    ])
  },
  beforeMount() {
    this.$store.commit("shareConfig");
  },
  mounted() {
    this.$nextTick(() => {
      this.$store.commit("getAudioManager", {
        id: this.$route.query.audioId,
        callback: res => {
          this.defaultImg = `${reqCfg.webUrl}static/img/default.png`;
          // 
          const shareDesc = `${res.data.alreadyHeardCount} ${res.data.snapCount}`;
          const shareLink = `${reqCfg.webUrl}#/Details?title=${encodeURIComponent(this.$route.query.title)}&audioId=${
            this.$route.query.audioId
          }&fromOpenId=${this.openId}`;
          this.$store.commit("userShare", {
            title: this.$route.query.title,
            desc: shareDesc,
            link: shareLink,
            imgUrl: res.data.imageUrl || reqCfg.imgUrl
          });
        }
      });
      this.$store.commit("getClassHourOfThieAudio", {
        id: this.$route.query.audioId,
        listSort: this.listSort,
        callback: res => {
          res.count ? this.getClassHour(0) : (this.showPage = true);
        }
      });
    });
  },
  beforeUpdate() {
    if (!refresh) {
      let audio = document.getElementById("audio");
      audio &&
        wx.ready(() => {
          audio.play();
        });
    }
  },
  watch: {
    flags(val, oldval) {
      document.getElementById("body").style.overflow = val ? "hidden" : "auto";
    },
    status(val, oldval) {
      let audio = document.getElementById("audio");
      if (val) {
        audio.play();
        this.roateStatus = true;
      } else {
        audio.pause();
        this.roateStatus = false;
      }
    },
    rangeValue(val, oldval) {
      refresh = true;
      this.roateStatus = false;
      if (val - oldval > 2 || val - oldval < 0) {
        let audio = document.getElementById("audio");
        audio.currentTime = this.rangeValue;
      }
    }
  },
  methods: {
    getClassHour(val) {
      if (this.whichOne == val) {
        this.status = !this.status;
        return;
      }
      this.status = false;
      // 
      let index = this.listSort ? val : this.classHourOfThieAudio.count - val - 1;
      if (!this.classHourOfThieAudio.list[index].free) {
        MessageBox({
          title: "",
          message: "<p class='color-00'>VIP<p>",
          showCancelButton: true
        }).then(res => {
          if (res === "confirm") {
            this.$store.commit("openPaylist");
          }
        });
        return;
      }
      this.whichOne = val;
      this.rangeValue = 0;
      this.maxValue = 0;
      this.$store.commit("getClassHourById", {
        id: this.classHourOfThieAudio.list[index].id,
        callback: res => {
          if (this.firstEnter) {
            if (window.name != "bencalie") {
              window.name = "bencalie";
              location.reload();
            } else {
              window.name = "";
            }
          }
          this.showIndicator("", "triple-bounce");
          this.status = true;
          this.Timeout = setTimeout(() => {
            refresh = true;
            this.status = true;
            this.firstEnter = false;
            this.closeIndicator();
          }, 1500);
        }
      });
    },
    // 
    snapCount() {
      if (this.singleClassHour.snap) {
        return;
      }
      this.singleClassHour.snap = true;
      this.$store.dispatch("snapCount", { id: this.$route.query.audioId, listSort: this.listSort });
    },
    // 
    timeupdate(e) {
      this.rangeValue = e.target.currentTime;
    },
    //
    playing() {
      // this.firstEnter = false
      this.status = true;
      this.$store.commit("getClassHourOfThieAudio", {
        id: this.$route.query.audioId,
        listSort: this.listSort,
        callback: res => {
          let audio = document.getElementById("audio");
          this.maxValue = audio.duration;
        }
      });
    },
    // 
    ended() {
      if (this.whichOne + 1 !== this.classHourOfThieAudio.count) {
        this.getClassHour(this.whichOne + 1);
      } else {
        this.status = false;
      }
    },
    // 
    Auditions() {
      !this.classHourOfThieAudio.count && this.Toast("");
      this.whichOne = -1;
      this.classHourOfThieAudio.count && this.getClassHour(0);
    },
    // 
    down() {
      this.flags = true;
      var touch;
      if (event.touches) {
        touch = event.touches[0];
      } else {
        touch = event;
      }
      this.position.x = touch.clientX;
      this.position.y = touch.clientY;
      this.dx = moveDiv.offsetLeft;
      this.dy = moveDiv.offsetTop;
    },
    move() {
      if (this.flags) {
        var touch;
        if (event.touches) {
          touch = event.touches[0];
        } else {
          touch = event;
        }
        this.nx = touch.clientX - this.position.x;
        this.ny = touch.clientY - this.position.y;
        this.xPum = this.dx + this.nx;
        this.yPum = this.dy + this.ny;
        this.xPum = this.xSize(this.xPum, 15, 65);
        this.yPum = this.ySize(this.yPum, 15, 105);
        moveDiv.style.left = this.xPum + "px";
        moveDiv.style.top = this.yPum + "px";
        //1.2  touchmove
        document.addEventListener(
          "touchmove",
          function() {
            // event.preventDefault();
          },
          false
        );
      }
    },
    // 
    xSize(x, l = 10, r = 10) {
      return x - l < 0 ? l : window.screen.availWidth - x > r ? x : document.body.clientWidth - r;
    },
    ySize(y, t = 10, b = 10) {
      return y - t < 0 ? t : window.screen.availHeight - y > b ? y : document.body.clientHeight - b;
    },
    //
    end() {
      this.flags = false;
    }
  }
};
</script>
<style scoped>
.w-index {
  width: 0.5rem;
}
.line-42 {
  line-height: 0.42rem;
}
.Details-tab {
  bottom: 0;
}
.backHome {
  top: 80%;
  right: 0.3rem;
  width: 1rem;
  height: 1rem;
}
.mar-t {
  margin-top: 1px;
}
.w-text {
  width: 1rem;
}
.border-h {
  border-bottom: 2px solid #2db9af;
}
</style>



