<template>
  <div class='padding-b-30'>
    <div class="flex text-left padding-t-b-10 height-40 margin-b-20">
      <div class="green-line"></div>
      <div class="f-28 color-red margin-l-30 flex"> </div>
    </div>
    <div class='margin-b-30'>
      <div class='flex height-88 margin-l-r-20 padding-l-r-10 border-radius-15 border-all border-color-ff' :class="{'bg-rgba-5-2d border-color':current==index}" v-for="(item,index) in price.list" :key='index' @click='current=index'>
        <div class='flex-1 f-34 text-ellipsis text-left'>{{item.vipProperty|VIPJurisdiction}}</div>
        <div class='text-throughLine color-b4 f-28 margin-r-20' v-if='item.IsOpenPreferential'>{{item.VIPMoney|formatToYuan}}</div>
        <div class='f-34 color-2d'>{{(item.IsOpenPreferential?item.VIPPreferentialMoney:item.VIPMoney)|formatToYuan}}</div>
      </div>
    </div>
    <button class='width-93p height-88 border-radius-15 bg-2d f-34 color-ff' @click='$store.commit("openPayDetails")'></button>
  </div>
</template>
<script>
import { mapGetters } from "vuex";
export default {
  name: "PriceList",
  data() {
    return {
      current: null
    };
  },
  computed: {
    ...mapGetters(["price", "currentPrice", "open"])
  },
  watch: {
    current(val, oldval) {
      this.$store.commit("setCurrentPrice", { num: val });
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.$store.commit("getVIPConfig", {
        callback: res => {
          this.current = 0;
        }
      });
    });
  }
};
</script>
<style scoped>
.bg-rgba-5-2d {
  background-color: #eaf8f7;
}
.border-color {
  border-color: #8bd8d3;
}
</style>



