import $ from "@/store/function";
import { Toast } from "mint-ui";

const user = {
  state: {
    edition: "v1.2.1",
    swipeHeight: 0,
    userIsVIP: {},
    userInfo: {},
    open: {
      payList: false,
      payDetails: false
    },
    qrcodeTicket: null
  },
  mutations: {
    // 
    setSwipeHeight: (state, val) => (state.swipeHeight = val),
    // openId
    getOpenId(state, val) {
      let obj = {},
        arr = location.search.slice(1).split("&");
      for (let i = 0, newarr = []; i < arr.length; i++) {
        newarr = arr[i].split("=");
        typeof obj[newarr[0]] === "undefined" && (obj[newarr[0]] = newarr[1]);
      }
      const json = { code: obj.code };
      if (!sessionStorage.getItem("openId")) {
        $.ajax(
          "get",
          "Mobile/getWXOpenId",
          json,
          res => {
            sessionStorage.setItem("openId", res.data.openId);
            val.callback && val.callback();
          },
          null,
          true
        );
      } else {
        val.callback && val.callback();
      }
    },
    // 
    getWXUserIsVIP(state) {
      var json = {
        openId: sessionStorage.getItem("openId")
      };
      $.ajax("get", "Mobile/getWXUserIsVIP", json, res => {
        state.userIsVIP = res.data;
      });
    },
    // 
    getWXUserInfo(state) {
      var json = {
        openId: sessionStorage.getItem("openId")
      };
      $.ajax(
        "get",
        "WeiXin/getWXUserInfo",
        json,
        res => {
          state.userInfo = res.data;
        },
        null,
        true
      );
    },
    // 
    openPaylist: state => (state.open.payList = !state.open.payList),
    // 
    openPayDetails: state => (state.open.payDetails = !state.open.payDetails),
    // 
    closePay: state => ((state.open.payList = false), (state.open.payDetails = false)),
    // 
    payVIPCode(state, val) {
      var json = {
        money: val.payMoney,
        commodityName: $.VIPJurisdiction(val.vipProperty),
        wxOpenId: sessionStorage.getItem("openId")
      }; //VIP
      $.ajax(
        "get",
        "/WeiXin/payVIPCode",
        json,
        res => {
          let order = res.data;
          WeixinJSBridge.invoke(
            "getBrandWCPayRequest",
            {
              appId: order.appId, //
              timeStamp: order.timeStamp, //1970
              nonceStr: order.nonceStr, //
              package: order.packagePrepay,
              signType: order.signType, //
              paySign: order.paySign //
            },
            res => {
              if (res.err_msg == "get_brand_wcpay_request:ok") {
                state.open.payList = false;
                state.open.payDetails = false;
                // ,
                //res.err_msgok
                var json1 = { orderId: order.orderId };
                $.ajax("get", "/WeiXin/getPayResult", json1, res => {
                  if (res.data) {
                    val.callback && val.callback(res);
                  } else {
                    alert("VIP");
                  }
                });
              }
            }
          );
        },
        res => {
          Toast("res.data.msg");
          val.callback && val.callback(res);
        },
        true,
        "...",
        res => {
          val.callback && val.callback(res);
        }
      );
    },
    // 
    getQrCode(state, val) {
      var json = { openId: val };
      $.ajax("get", "WeiXin/getQRCode", json, res => {
        state.qrcodeTicket = res.data;
      });
    }
  }
};

export default user;



