<template>
    <div class="flex bg-ff">
        <img class="w-h-img border-radius-50p margin-r-20 border-all" :src="`${userInfo.headimgurl||require('@/assets/image_header@3x.png')}`">
        <div class="flex-1 text-left">
            <div class="f-34 color-00 margin-t-10 text-ellipsis" :class="{'height-88':userInfo.vip}">{{userInfo.nickname}}</div>
            <div class="f-28 color-2d margin-t-10" v-if='!userInfo.vip'></div>
        </div>
        <div v-if='userInfo.vip' class="iconfont icon-mark_VIP1 f-50 margin-t-20 color-2d"></div>
    </div>
</template>
<script>
import { mapGetters } from "vuex";
export default {
  name: "VIPNav",
  computed: {
    ...mapGetters(["userInfo", "edition", "shareCfg", "reqCfg"])
  },
  mounted() {
    this.$nextTick(() => {
      this.$store.commit("userShare", {
        title: this.shareCfg.title,
        desc: this.shareCfg.desc,
        link: `${this.reqCfg.webUrl}?fromOpenId=${sessionStorage.getItem("openId")}`,
        imgUrl: this.shareCfg.imgUrl
      });
    });
  }
};
</script>



