<template>
  <div>
    <div v-infinite-scroll="loadMore(friend.loading)" infinite-scroll-disabled="friend.loading" infinite-scroll-distance="10">
      <div class="bg-ff margin-t-30">
        <VIPNav class='padding-all-30'/>
        <div class='height-88 bg-ff border-t flex' @click='(userInfo.vipProperty!=-1)&&pushPage("VIPDetails")' v-if='userInfo.vip'>
          <div class='f-34 margin-l-30 flex-1 text-left'>VIP</div>
          <div class='f-32 color-2d margin-r-30'>{{userInfo.vipProperty==-1?'':'VIP'}}</div>
          <div class='f-32 color-2d margin-r-10' v-if='userInfo.vipExpireDate&&(userInfo.vipProperty!=-1)'>{{userInfo.vipExpireDate|formatYMD}}</div>
          <div v-if='userInfo.vipProperty!=-1' class='iconfont icon-disclosure_arrow_gra margin-r-30 color-b4'></div>
        </div>
      </div>
      <div class="margin-t-30 bg-ff" v-if='!userInfo.vip'>
        <PriceList />
      </div>
      <div class="margin-t-30" v-else>
        <img class="width-100p" :src="vipImg">
      </div>
      <div class="margin-t-30 bg-ff">
        <QRFriends></QRFriends>
      </div>
      <div class='height-88 color-b4'>{{edition}}</div>
    </div>
  </div>
</template>
<script>
import { mapGetters } from "vuex";
export default {
  name: "VIP",
  data() {
    return {
      title: "",
      vipImg: `${reqCfg.webUrl}static/img/vip.png`
    };
  },
  computed: {
    ...mapGetters(["userInfo", "edition", "friend"])
  }
};
</script>


