import axios from "axios";
import { Toast, Indicator } from "mint-ui";
const $ = {
  ajax(type, url, json = null, okFun = null, errFun = null, isIndicator = false, text = "...", errorFun = null) {
    let objChangeStr = function(obj) {
      var retVal = "";
      for (let key in obj) {
        retVal += `${key}=${obj[key]}&`;
      }
      return retVal.slice(0, retVal.length - 1);
    };
    isIndicator && Indicator.open({ text, spinnerType: "fading-circle" });
    var urlJson = type == "get" ? `${reqCfg.requestUrl}${url}?${objChangeStr(json)}` : `${reqCfg.requestUrl}${url}`;
    axios[type](urlJson, type == "post" && json, {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json"
      }
    })
      .then(res => {
        isIndicator && Indicator.close();
        const data = JSON.parse(res.request.responseText);
        if (data.code == 0) {
          okFun && okFun(data);
        } else {
          if (data.code == 1) {
            window.location.href = `https://open.weixin.qq.com/connect/oauth2/authorize?appid=${
              reqCfg.appId
            }&redirect_uri=${encodeURIComponent(
              location.href.replace("?from=singlemessage", "")
            )}&response_type=code&scope=snsapi_base&state=1#wechat_redirect`;
          } else if (data.code == 9001) {
            let obj = {};
            let arr =
              location.href.split("?").length == 3
                ? location.href.split("#/Details?")[1].split("&")
                : location.search.slice(1).split("&");
            for (let i = 0, newarr = []; i < arr.length; i++) {
              newarr = arr[i].split("=");
              typeof obj[newarr[0]] === "undefined" && (obj[newarr[0]] = newarr[1]);
            }
            window.vue.pushPage("QrCode", { fromOpenId: obj.fromOpenId });
          } else {
            errFun ? errFun(res) : Toast({ message: data.msg, duration: 2000 });
          }
        }
      })
      .catch(res => {
        isIndicator && Indicator.close();
        errorFun && errorFun();
        res.toString().indexOf("Network Error") > -1 && Toast({ message: "", duration: 2000 });
      });
  },
  VIPJurisdiction(val) {
    let ret = null;
    if (val == -1) {
      ret = "VIP";
    } else if (val % 12 == 0) {
      ret = `${val / 12}VIP`;
    } else {
      ret = `${val}VIP`;
    }
    return ret;
  }
};
export default $;



