import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

import user from "./modules/user";
import getDate from "./modules/getDate";
import share from "./modules/share";

import getters from "./getters";

export default new Vuex.Store({
  modules: {
    user,
    getDate,
    share
  },
  getters
});



