// 

const getters = {
  // user
  edition: state => state.user.edition,
  swipeHeight: state => state.user.swipeHeight,
  userIsVIP: state => state.user.userIsVIP,
  userInfo: state => state.user.userInfo,
  open: state => state.user.open,
  qrcodeTicket: state => state.user.qrcodeTicket,
  // getDate
  homeBanner: state => state.getDate.homeBanner,
  update: state => state.getDate.update,
  homeDate: state => state.getDate.homeDate,
  cateGory: state => state.getDate.cateGory,
  audioManager: state => state.getDate.audioManager,
  courseInformation: state => state.getDate.courseInformation,
  classHourOfThieAudio: state => state.getDate.classHourOfThieAudio,
  singleClassHour: state => state.getDate.singleClassHour,
  friend: state => state.getDate.friend,
  price: state => state.getDate.price,
  currentPrice: state => state.getDate.currentPrice,
  payMoney: state =>
    state.getDate.currentPrice.IsOpenPreferential
      ? state.getDate.currentPrice.VIPPreferentialMoney
      : state.getDate.currentPrice.VIPMoney,
  // share
  reqCfg: state => state.share.reqCfg,
  shareCfg: state => state.share.shareCfg,
  qrCfg: state => state.share.qrCfg
};

export default getters;



