<template>
  <div id="app">
    <div v-show='show'>
      <router-view :class="{'padding-b-120':selected=='Home'||selected=='VIP'}" />
      <mt-tabbar fixed v-model="selected" class="border-t" v-show="selected=='Home'||selected=='VIP'">
        <mt-tab-item :id="item.id" v-for="(item,index) in tabList" :key='index'>
          <div class="iconfont icon-tabbar_index f-60 color-b4 selected" :class='item.icon'></div>
          <div class="color-b4 selected">{{item.name}}</div>
        </mt-tab-item>
      </mt-tabbar>
      <GreatVip />
      <Pay class='z-1' />
    </div>
  </div>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      show: false,
      selected: "Home",
      tabList: [
        {
          id: "Home",
          icon: "icon-tabbar_index",
          name: ""
        },
        {
          id: "VIP",
          icon: "icon-tabbar_vip",
          name: ""
        }
      ]
    };
  },
  watch: {
    $route(to, from) {
      this.$store.commit("closePay");
      this.get();
      setTimeout(() => {
        this.scrollTop();
      }, 500);
    },
    selected(val, oldval) {
      (val == "Home" || val == "VIP") && this.pushPage(val);
    }
  },
  beforeMount() {
    this.$store.commit("shareConfig");
  },
  mounted() {
    this.$nextTick(() => {
      // sessionStorage.setItem("openId", "oaJVVw6yfHyt7vHQ-6Pn4WdtzgIc");
      this.$store.commit("getOpenId", {
        callback: () => {
          this.get();
        }
      });
    });
  },
  methods: {
    get() {
      this.$store.commit("getWXUserIsVIP");
      this.$store.commit("getWXUserInfo");
      this.selected = location.href.split("#/")[1].split("?")[0];
      this.show = sessionStorage.getItem("openId") ? true : false;
    }
  }
};
</script>

<style>
/*  */
.z-1 {
  z-index: 333;
}
.mint-indicator,
.mint-toast {
  position: fixed !important;
  z-index: 9999 !important;
}
/*  */
.color-2d {
  color: #2db9af;
}
.bg-2d {
  background-color: #2db9af;
}
.border-color-2d {
  border-color: #2db9af;
}
.mint-header-title {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-weight: bold;
}
.mint-button {
  width: 0.3rem !important;
}
.mt-range-thumb,
.mt-range-progress {
  background-color: #2db9af !important;
}
.mt-range-content {
  margin-right: 10px !important;
}
.mt-range-runway {
  right: -12px !important;
}
.mt-range-thumb {
  top: 0.14rem !important;
  width: 0.3rem !important;
  height: 0.3rem !important;
}
.mint-swipe-indicator {
  background-color: #fff !important;
  opacity: 0.6 !important;
}
.mint-swipe-indicators {
  bottom: 3px !important;
}
.mint-swipe-indicators .is-active {
  background-color: #2db9af !important;
  opacity: 1 !important;
}
.mint-tab-item {
  background-color: #fff !important;
}
.mint-loadmore {
  overflow: visible !important;
}
.mint-loadmore-bottom {
  height: 0 !important;
}
.is-selected .selected {
  color: #2db9af !important;
}
.green-line {
  width: 0.06rem;
  background-color: #2db9af;
}
/*  */
.w-h-img {
  width: 1rem;
  height: 1rem;
}
.w-h-QRCode {
  width: 2.76rem;
  height: 2.76rem;
}
.w-h-header {
  width: 0.6rem;
  height: 0.6rem;
}
.btn-shopping {
  width: 1rem;
  height: 0.5rem;
}
</style>



