import Home from "@/components/mobile/Home";
import AllClassification from "@/components/mobile/AllClassification";
import Category from "@/components/mobile/Category";
import Details from "@/components/mobile/Details";
import VIP from "@/components/mobile/VIP";
import VIPDetails from "@/components/mobile/VIPDetails";
import QrCode from "@/components/mobile/QrCode";

const routes = [
  {
    path: "/Home",
    name: "Home",
    component: Home
  },
  {
    path: "/AllClassification",
    name: "AllClassification",
    component: AllClassification
  },
  {
    path: "/Category",
    name: "Category",
    component: Category
  },
  {
    path: "/Details",
    name: "Details",
    component: Details
  },
  {
    path: "/VIP",
    name: "VIP",
    component: VIP
  },
  {
    path: "/VIPDetails",
    name: "VIPDetails",
    component: VIPDetails
  },
  {
    path: "/QrCode",
    name: "QrCode",
    component: QrCode
  },
  {
    path: "*",
    redirect: "/Home"
  }
];

export default routes;



