import $ from "@/store/function";
const getDate = {
  state: {
    homeBanner: { list: [], count: 0 },
    update: { list: [], count: 0 },
    homeDate: { list: [], count: 0 },
    cateGory: { list: [], count: 0 },
    audioManager: { list: [], allLoaded: false, count: 0 },
    courseInformation: {},
    classHourOfThieAudio: { list: [], count: 0 },
    singleClassHour: {},
    friend: { list: [], count: 0, pageNum: 1, loading: true },
    price: { list: [], count: 0 },
    currentPrice: {}
  },
  mutations: {
    // 
    getBannerList(state, val) {
      $.ajax("get", "Mobile/getBannerList", null, res => {
        state.homeBanner.list = res.rows;
        state.homeBanner.count = res.count;
        val.callback && val.callback(res);
      });
    },
    // 
    getLatestUpdateAudio(state, val) {
      var json = {
        num: val.num
      };
      $.ajax("get", "Mobile/getLatestUpdateAudio", json, res => {
        state.update.list = res.rows;
        state.update.count = res.count;
        val.callback && val.callback(res);
      });
    },
    // 
    getHomeAudioManager(state, val) {
      var json = {
        cateGoryNum: 0,
        audioNum: val.num
      };
      $.ajax("get", "Mobile/getHomeAudioManager", json, res => {
        state.homeDate.list = res.rows;
        state.homeDate.count = res.count;
        val.callback && val.callback(res);
      });
    },
    // 
    getCateGoryList(state, val) {
      $.ajax("post", "Mobile/getCateGoryList", null, res => {
        state.cateGory.list = res.rows;
        state.cateGory.count = res.count;
        val.callback && val.callback(res);
      });
    },
    // 
    getAudioManagerList(state, val) {
      var json = {
        type: val.id,
        page: val.page,
        pageSize: 6
      };
      $.ajax(
        "get",
        val.id ? "Mobile/getAudioManagerList" : "Mobile/getLatestUpdateAudioPage",
        json,
        res => {
          val.page == 1 ? (state.audioManager.list = res.rows) : state.audioManager.list.push(...res.rows);
          state.audioManager.allLoaded = state.audioManager.list.length >= res.count;
          state.audioManager.count = res.count;
          val.callback && val.callback(res);
        },
        null,
        val.refresh ? true : false
      );
    },
    // 
    getAudioManager(state, val) {
      var json = {
        id: val.id,
        openId: sessionStorage.getItem("openId")
      };
      $.ajax("get", "Mobile/getAudioManager", json, res => {
        state.courseInformation = res.data;
        val.callback && val.callback(res);
      });
    },
    // 
    getClassHourOfThieAudio(state, val) {
      var json = {
        audioId: val.id,
        wxOpendId: sessionStorage.getItem("openId")
      };
      $.ajax("get", "Mobile/getClassHourOfThieAudio", json, res => {
        state.classHourOfThieAudio.list = val.listSort ? res.rows : res.rows.reverse();
        state.classHourOfThieAudio.count = res.count;
        val.callback && val.callback(res);
      });
    },
    // /
    reverseClassHourOfThieAudio(state) {
      state.classHourOfThieAudio.list.reverse();
    },
    // 
    getClassHourById(state, val) {
      var json = {
        id: val.id,
        wxOpendId: sessionStorage.getItem("openId")
      };
      $.ajax("get", "Mobile/getClassHourById", json, res => {
        state.singleClassHour = res.data;
        val.callback && val.callback(res);
      });
    },
    // vip
    getVIPConfig(state, val) {
      $.ajax(
        "get",
        "Mobile/getVIPConfig",
        null,
        res => {
          state.price.list = res.rows;
          state.price.count = res.count;
          val.callback && val.callback(res);
        },
        null,
        val.refresh ? true : false
      );
    },
    // 
    setCurrentPrice(state, val) {
      state.currentPrice = state.price.list[val.num];
      val.callback && val.callback();
    },
    // 
    getShareDetailsManager(state, val) {
      val.pageNum == 1 && (state.friend.pageNum = 1);
      let json = { openId: sessionStorage.getItem("openId"), page: state.friend.pageNum, pageSize: 5 };
      $.ajax("get", "WeiXin/getShareDetailsManager", json, res => {
        state.friend.list = state.friend.pageNum == 1 ? res.rows : state.friend.list.concat(res.rows);
        state.friend.loading = res.count <= state.friend.list.length;
        state.friend.count = res.count;
        state.friend.pageNum++;
        val.callback && val.callback(res);
      });
    },
    // 
    stopGetFriend(state) {
      state.friend.loading = true;
    }
  },
  actions: {
    // 
    snapCount({ commit, state }, val) {
      var json = {
        classHourId: state.singleClassHour._id,
        openId: sessionStorage.getItem("openId")
      };
      $.ajax("get", "Mobile/snapCount", json, res => {
        commit("getClassHourOfThieAudio", val);
      });
    }
  }
};

export default getDate;



