<template>
  <div v-wechat-title="title">
    <div class="over-scroll" ref="wrapper" :style="{ height: wrapperHeight + 'px' }">
      <mt-loadmore :top-method="loadTop" :bottom-method="loadBottom" :bottomPullText="bottomPullText" :bottom-all-loaded="audioManager.allLoaded" :autoFill=false ref="loadmore">
        <div class="flex bg-ff border-b padding-all-30" @click="onClick_onPage(item)" v-for="(item,index) in audioManager.list" :key="index">
          <div class="img border-radius-20 over-hidden margin-r-20" :style="{backgroundImage: 'url('+(item.imageUrl||defaultImg)+')'}"></div>
          <div class="flex-1">
            <div class="f-34 text-left color-33 Category-height-title over-scroll folding text-justify">{{item.title}}</div>
            <div class="text-left margin-t-20 f-24 color-b4">{{item.uploadDate|formatYMD}}</div>
            <div class="flex color-b4 f-24 height-33 margin-t-20">
              <div class="iconfont icon-detail_play f-20"></div>
              <div class="margin-r-30">{{item.numberOfHours}}</div>
              <div class="iconfont icon-detail_listened f-24 margin-r-10"></div>
              <div class="margin-r-30 flex">
                <span class="f-24">{{item.alreadyHeardCount}}</span>
              </div>
              <div class="iconfont icon-detail_good f-24 margin-r-10"></div>
              <div class="flex">
                <span class="f-24">{{item.snapCount}}</span>
              </div>
            </div>
          </div>
        </div>
      </mt-loadmore>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "Category",
  data() {
    return {
      title: "",
      // 
      wrapperHeight: 0,
      bottomPullText: "", //
      pageSize: 1,
      // 
      defaultImg: `${reqCfg.webUrl}static/img/default.png`
    };
  },
  computed: {
    ...mapGetters(["audioManager", "shareCfg", "reqCfg"])
  },
  beforeMount() {
    this.$store.commit("shareConfig");
  },
  mounted() {
    this.$nextTick(() => {
      this.title = this.$route.query.title;
      this.wrapperHeight = document.documentElement.clientHeight;
      this.$store.commit("getAudioManagerList", {
        id: this.$route.query.categoryId,
        page: this.pageSize,
        refresh: true
      });
      this.$store.commit("userShare", {
        title: this.shareCfg.title,
        desc: this.shareCfg.desc,
        link: `${this.reqCfg.webUrl}?fromOpenId=${sessionStorage.getItem("openId")}`,
        imgUrl: this.shareCfg.imgUrl
      });
    });
  },
  methods: {
    onClick_onPage(Single) {
      this.pushPage("Details", {
        title: Single.title,
        audioId: Single.id
      });
    },
    // 
    loadTop() {
      this.pageSize = 1;
      setTimeout(() => {
        this.$store.commit("getAudioManagerList", {
          id: this.$route.query.categoryId,
          page: this.pageSize,
          callback: () => {
            this.$refs.loadmore.onTopLoaded();
          }
        });
      }, 1000);
    },
    // 
    loadBottom() {
      this.pageSize += 1;
      setTimeout(() => {
        this.$store.commit("getAudioManagerList", {
          id: this.$route.query.categoryId,
          page: this.pageSize,
          callback: () => {
            setTimeout(() => {
              this.$refs.loadmore.onBottomLoaded();
            }, 300);
          }
        });
      }, 1000);
    }
  }
};
</script>

<style scoped>
.height-33 {
  height: 0.33rem;
  line-height: 0.33rem;
}
.margint20 {
  margin-top: 0.3rem;
}
.img {
  width: 1.8rem;
  height: 1.8rem;
  background: url() no-repeat center center;
  background-size: auto 100%;
}
.Category-height-title {
  height: 0.8rem;
  line-height: 0.4rem;
}
.folding {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;

  /* display:-webkit-box;
    -webkit-line-clamp:3;
    -webkit-box-orient:vertical;
    overflow:hidden; */
}
</style>


