<template>
    <div>
        <mt-popup v-model="payList" class='width-100p height-60p' position='bottom'>
            <mt-header title="VIP" class="bg-ff color-00 f-36 height-88 border-b padding-l-r-25">
                <mt-button slot="left">
                    <div class="iconfont icon-close f-30" @click='$store.commit("openPaylist",false)'></div>
                </mt-button>
            </mt-header>
            <div class='border-b over-scroll h-priceList'>
                <div class='flex height-88 margin-l-30 padding-r-30 flex-align-center' :class="{'border-t':index}" v-for='(item,index) in price.list' :key='index'>
                    <div class='flex-1 f-34 text-ellipsis text-left'>{{item.vipProperty|VIPJurisdiction}}</div>
                    <div class='text-throughLine color-b4 f-28 margin-r-20' v-if='item.IsOpenPreferential'>{{item.VIPMoney|formatToYuan}}</div>
                    <div class='f-34 color-2d margin-r-20'>{{(item.IsOpenPreferential?item.VIPPreferentialMoney:item.VIPMoney)|formatToYuan}}</div>
                    <div class='bg-2d color-ff border-radius-20 shopping' @click='$store.commit("setCurrentPrice", { num: index });$store.commit("openPayDetails")'></div>
                </div>
            </div>
            <div class='color-2d margin-t-20 f-34'>VIP</div>
        </mt-popup>
        <mt-popup v-model="payDetails" class='width-100p height-60p' position='bottom'>
            <mt-header title="" class="bg-ff color-00 f-36 height-88 border-b padding-l-r-25">
                <mt-button slot="left">
                    <div class="iconfont icon-close f-30" @click='$store.commit("openPayDetails")'></div>
                </mt-button>
            </mt-header>
            <div class="padding-t-b-30">
                <div class="f-56 color-00 margin-t-30">{{payMoney|formatToYuan}}</div>
                <div class="f-28 color-red padding-t-b-10 text-throughLine" v-show='currentPrice.IsOpenPreferential'>{{currentPrice.VIPMoney|formatToYuan}}</div>
                <div class="f-28 color-2d margin-b-30">VIP</div>
            </div>
            <div class="border-t-b">
                <div class="flex height-88 f-34 color-00 margin-l-30 padding-r-30 border-b">
                    <div class="flex-1 text-left"></div>
                    <div>{{currentPrice.vipProperty|VIPJurisdiction}}</div>
                </div>
                <div class="flex height-88 f-34 color-00 margin-l-30 padding-r-30">
                    <div class="flex-1 text-left"></div>
                    <div></div>
                </div>
            </div>
            <button class="width-90p bg-2d color-ff height-90 f-34 border-radius-20 btn-paymaney p-absolute" type="button" :disabled="btnStatus" @click='payVipCodeRequest()'></button>
        </mt-popup>
    </div>
</template>
<script>
import { mapGetters } from "vuex";
export default {
  name: "Pay",
  data() {
    return {
      payList: false,
      payDetails: false,
      orderId: "",
      btnStatus: false
    };
  },
  computed: {
    ...mapGetters(["price", "open", "currentPrice", "payMoney"])
  },
  watch: {
    ["open.payList"](val, oldval) {
      this.payList = val;
      val && this.$store.commit("getVIPConfig", { refresh: true });
    },
    ["open.payDetails"](val, oldval) {
      this.payDetails = val;
    }
  },
  methods: {
    payVipCodeRequest() {
      this.btnStatus = true;
      this.$store.commit("payVIPCode", {
        payMoney: this.payMoney,
        vipProperty: this.currentPrice.vipProperty,
        callback: res => {
          this.$store.commit("getWXUserIsVIP");
          this.$store.commit("getWXUserInfo");
          this.btnStatus = false;
        }
      });
    }
  }
};
</script>
<style scoped>
.shopping {
  width: 1rem;
  height: 0.38rem;
  line-height: 0.4rem;
}
.h-priceList {
  max-height: 80%;
}
.btn-paymaney {
  left: 5%;
  bottom: 0.4rem;
}
</style>



