<template>
  <mt-popup v-model="popupVisible">
    <div class='GreatVip p-fixed width-100p height-100p rgba-7'>
      <div class="Great-img f-30 color-2d" :style="{backgroundImage: `url(${bgImg})`}">
        <div class="content-text">
          <div>{{userInfo.inviteCount}}</div>
          <div>{{userInfo.vipProperty|VIPJurisdiction}}</div>
        </div>
        <button class="GreatVipBtn f-30 color-2d bg-ff border-all border-color-2d" @click='popupVisible=false'></button>
      </div>
    </div> 
  </mt-popup>
</template>
<script>
import { mapGetters } from "vuex";
export default {
  name: "GreatVip",
  data() {
    return {
      bgImg: `${reqCfg.webUrl}static/img/invite_success@3x.png`,
      popupVisible: false
    };
  },
  computed: {
    ...mapGetters(["userInfo", "userIsVIP"])
  },
  watch: {
    userIsVIP(val, oldval) {
      this.popupVisible = val.remind;
    }
  }
};
</script>
<style scoped>
.GreatVip {
  z-index: 11;
  top: 0;
  left: 0;
}
.Great-img {
  width: 6rem;
  height: 5rem;
  position: absolute;
  top: 50%;
  margin-top: -2.5rem;
  left: 50%;
  margin-left: -3rem;
  background: url() no-repeat center center;
  background-size: 100% 100%;
}
.content-text {
  margin-top: 2.2rem;
}
.GreatVipBtn {
  margin-top: 1rem;
  width: 3rem;
  height: 0.7rem;
  border-radius: 3rem;
}
</style>


