<template>
  <div>
    <div class="flex text-left padding-t-b-10 height-40 margin-b-20">
      <div class="green-line"></div>
      <div class="f-28 color-red margin-l-30 flex" v-if='(!userInfo.vip||showfriendnum)&&currentPrice'>
        <div v-if='currentPrice.vipProperty==-1'>{{currentPrice.InviteCount}} VIP</div>
        <div v-else-if='currentPrice.vipProperty%12==0'>{{currentPrice.InviteCount}} {{currentPrice.vipProperty/12}}VIP</div>
        <div v-else>{{currentPrice.InviteCount}} {{currentPrice.vipProperty}}VIP</div>
      </div>
      <div class="f-28 color-red margin-l-30 flex" v-else></div>
    </div>
    <img class="w-h-QRCode" v-if='userInfo.qrcodeTicket' :src="`https://mp.weixin.qq.com/cgi-bin/showqrcode?ticket=${userInfo.qrcodeTicket}`">
    <div class="f-28 color-2d padding-b-30"></div>
    <div class="margin-t-20 border-t">
      <div class="flex margin-l-30 padding-r-30 height-100 text-ellipsis" :class="{'border-t':index}" v-for='(item,index) in friend.list' :key='index'>
        <div class='color-b4 margin-l-r-20'>{{index+1}}</div>
        <img class="w-h-header border-radius-50p margin-t-20 margin-r-15 border-all" :src="`${item.headimgurl||require('@/assets/image_header@2x.png')}`">
        <div class="f-28 color-96 flex-1 text-ellipsis text-left">{{item.nickname}}</div>
      </div>
    </div>
  </div>
</template>
<script>
import { mapGetters } from "vuex";
export default {
  name: "QRFriends",
  props: ["showfriendnum"],
  computed: {
    ...mapGetters(["userInfo", "currentPrice", "friend"])
  },
  mounted() {
    this.$nextTick(() => {
      this.$store.commit("getShareDetailsManager", { pageNum: 1 });
    });
  }
};
</script>


