<template>
  <div v-wechat-title="title">
    <mt-swipe :style="{height: swipeHeight +'px'}" :showIndicators="homeBanner.count!==1">
      <mt-swipe-item v-for="(item,index) in homeBanner.list" :key="index">
        <img class="margin-0-auto max-width-100p swipeImg" :src="item.bannerImageUrl">
      </mt-swipe-item>
    </mt-swipe>
    <div class='margin-t-30'>
      <div class="bg-ff padding-b-30 clearfix">
        <div class="fl width-25p" v-for="(item,index) in homeDate.list" :key="index" @click="onClick_onPage(item)" v-if="index<7">
          <div class="iconfont margin-t-30 f-50 color-2d" :class="item.homeLogoName||'icon-icon_index_recommend'"></div>
          <div class="f-26 color-00">{{item.cateGoryName}}</div>
        </div>
        <div class="fl width-25p" @click="pushPage('AllClassification',{title: ''})">
          <div class="iconfont margin-t-30 f-50 icon-icon_index_alltype color-2d"></div>
          <div class="f-26 color-00"></div>
        </div>
      </div>
    </div>
    <div class='margin-t-30'>
      <div class="flex bg-ff height-88 padding-l-r-30 border-b">
        <div class="iconfont f-32 margin-r-10 icon-icon_list_new color-2d"></div>
        <div class="flex-1 text-left f-34 color-00"></div>
        <div class="flex">
          <div class="color-2d f-26" @click="pushPage('Category',{title:''})"></div>
          <div class="iconfont icon-disclosure_arrow_gra f-26 color-2d"></div>
        </div>
      </div>
      <List :enterList="update.list" v-if='update.count'></List>
      <div class="bg-ff f-32 color-b4 height-88" v-else></div>
    </div>
    <div class="margin-t-30" v-for="(item,index) in homeDate.list" :key="index">
      <div class="flex bg-ff height-88 padding-l-r-30 border-b">
        <div class="iconfont f-32 margin-r-10 color-2d" :class="item.logoName||'icon-icon_list_recommend'"></div>
        <div class="flex-1 text-left f-34 color-00">{{item.cateGoryName}}</div>
        <div class="flex" v-if='item.list.length'>
          <div class="color-2d f-26" @click="onClick_onPage(item)"></div>
          <div class="iconfont icon-disclosure_arrow_gra f-26 color-2d"></div>
        </div>
      </div>
      <List :enterList="item.list" v-if='item.list.length'></List>
      <div class="bg-ff f-32 color-b4 height-88" v-else></div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "Home",
  data() {
    return {
      title: "",
      // 
      defaultNum: 5
    };
  },
  computed: {
    ...mapGetters(["swipeHeight", "homeBanner", "update", "homeDate", "shareCfg", "reqCfg"])
  },
  mounted() {
    this.$nextTick(() => {
      // this.homeBanner.count && this.swipeH();
      this.$store.commit("getBannerList", {
        callback: res => {
          this.swipeH(res.rows[0].bannerImageUrl);
        }
      });
      this.$store.commit("getLatestUpdateAudio", { num: this.defaultNum });
      this.$store.commit("getHomeAudioManager", { num: this.defaultNum });
      this.$store.commit("userShare", {
        title: this.shareCfg.title,
        desc: this.shareCfg.desc,
        link: `${this.reqCfg.webUrl}?fromOpenId=${sessionStorage.getItem("openId")}`,
        imgUrl: this.shareCfg.imgUrl
      });
    });
  },
  methods: {
    // 
    swipeH(img_url) {
      let img = new Image();
      img.src = img_url;
      let int = setInterval(() => {
        if (img.width > 0) {
          let bl = img.height / img.width;
          let clientWidth = document.documentElement.clientWidth;
          this.$store.commit("setSwipeHeight", (clientWidth - 0) * bl);
          clearInterval(int);
        }
      }, 50);
    },
    // 
    onClick_onPage(item) {
      if (!item.list.length) {
        this.Toast("");
        return;
      }
      this.pushPage("Category", {
        title: item.cateGoryName,
        categoryId: item.cateGoryId
      });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



