<template>
  <div class="bg-ff">
    <div class="flex flex-align-center margin-l-30 padding-t-b-10 padding-r-30" @click="onClick_goDetail(item)" :class="{'border-t':index}" v-for="(item,index) in enterList" :key="index">
      <div class="iconfont icon-icon_list_audio f-60 margin-r-20 color-2d"></div>
      <div class="flex-1 text-left text-ellipsis">
        <div class="text-ellipsis f-34 color-33">{{item.title}}</div>
        <div class="flex flex-align-center padding-t-10 f-24 color-b4">
          <div class="iconfont icon-detail_listened f-24 margin-r-10"></div>
          <div class="margin-r-30">{{item.alreadyHeardCount}}</div>
          <div class="iconfont icon-detail_good f-24 margin-r-10"></div>
          <div>{{item.snapCount}}</div>
        </div>
      </div>
      <div class="f-24 color-b4 margin-l-20">{{item.uploadDate|formatYMD}}</div>
    </div>
  </div>
</template>

<script>
export default {
  name: "List",
  props: ["enterList"],
  methods: {
    onClick_goDetail(item) {
      this.pushPage("Details", {
        title: item.title,
        audioId: item.id
      });
    }
  }
};
</script>


